                description: "Nietzsche's treatise outlines his thoughts \"on the origin of our moral prejudices\" previously given brief expression in his Human, All Too Human (1878). Nietzsche attributes the desire to publish his \"hypotheses\" on the origins of morality to reading his friend Paul Rée's book The Origin of the Moral Sensations (1877) and finding the \"genealogical hypotheses\" offered there unsatisfactory.\n" +
                    "\n" +
                    "Nietzsche decided that \"a critique of moral values\" was needed, that \"the value of these values themselves must be called into question\". To this end Nietzsche provides a history of morality, rather than a hypothetical account in the style of Rée, whom Nietzsche classifies as an \"English psychologist\"[2] (using \"English\" to designate an intellectual temperament, as distinct from a nationality)."
